.card {
  position: relative;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: white;
  display: inline-block;
  width: 250px;
  height: 400px;
  margin: 20px;
  border: solid 1px #effffa;
  border-radius: 50px 0px 50px 0px;
  -moz-border-radius: 50px 0px 50px 0px;
  -webkit-border-radius: 50px 0px 50px 0px;
  transition: all 500ms;
}
#root {
  margin-left: 0;
  text-align: center;
}
.card:hover {
  -webkit-box-shadow: 0px 0px 35px 3px rgba(0,0,0,0.53);
  -moz-box-shadow: 0px 0px 35px 3px rgba(0,0,0,0.53);
  box-shadow: 0px 0px 35px 3px rgba(0,0,0,0.53);
  background-blend-mode: multiply; 
  border: solid 1px transparent;
  border-radius: 0px 50px 0px 50px;
  -moz-border-radius: 0px 50px 0px 50px;
  -webkit-border-radius: 0px 50px 0px 50px;
  background-color: gray;
}
.card:active {
  border-radius: 50px 50px 50px 50px;
  -moz-border-radius: 50px 50px 50px 50px;
  -webkit-border-radius: 50px 50px 50px 50px;
  background-color: rgb(71, 71, 71);
}
.Ddiv {
  position: inherit;
  top: 0px;
  left: 0px;
  margin: 5px;
  border: none;
  color: white;
  font-weight: lighter;
  text-align: left;
  font-size: 18px;
  visibility: hidden;
  transition: visibility 200ms, top 500ms, color 500ms, font-size 500ms;
}
.card:hover > .Ddiv {
  visibility: visible;
  top: 200px; 
  color: white; 
}
p {
  text-align: right;
  display: inline;
  position: absolute;
  right: 5px;
  margin: 0px;
  color: rgb(162, 244, 255);
}
.footer {
  position: absolute;
  bottom: 15px;
  left: 10px;
  color: #393c45;
  font-size: 25px;
  transition: all 500ms;
}
.card:hover > .footer {
  color: rgb(162, 244, 255);
  text-align: center;
  bottom: 250px;

}
.card:hover h6, .card:hover h5, .card:hover h4 {
  color: rgb(162, 244, 255);
  text-align: center;
  bottom: 250px;
  display: inline-block;
}
h4 {
  margin: 0mm;
  text-align: left;
}
h5 {
  color: #48d2c1;
  margin: 0mm;
  margin-top: 5px;
  text-align: left;
  transition: all 500ms;
}
h6 {
  margin: 0px;
  margin-top: 5px;
  color: #51575b;
  text-align: left;
  transition: all 500ms;
}
#searchInput {
  display: inline-block;
  margin: auto;
  margin-bottom: 10px;
  margin-top: 5px;
  font-size: 20px;
  border-radius: 20px 0px 20px 0px;
  -moz-border-radius: 20px 0px 20px 0px;
  -webkit-border-radius: 20px 0px 20px 0px;
  border: solid 1px #cecece;
  padding: 10px;
  width: 220px;
  -webkit-box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  -moz-box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  transition: all 500ms;
}
#searchInput:focus {
  width: 240px;
  border-radius: 0px 20px 0px 20px;
  -moz-border-radius: 0px 20px 0px 20px;
  -webkit-border-radius: 0px 20px 0px 20px;
  -webkit-box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
  -moz-box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
  box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
  background-color: white;
}
#searchInput:hover {
  width: 240px;
  border-radius: 0px 20px 0px 20px;
  -moz-border-radius: 0px 20px 0px 20px;
  -webkit-border-radius: 0px 20px 0px 20px;
  -webkit-box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
  -moz-box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
  box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
}
.srchConrainer {
  position: fixed;
  right: 0px;
  top: 0px;
  z-index: 1;
  background-color: #e7e7e7;
  width: 100%;
  text-align: center;
  -webkit-box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.10);
  -moz-box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.10);
  box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.10);
  transition: top 300ms;
}
#gram {
  font-weight: bold;
  text-align: center;
  width: 45px;
  border-radius: 5px;
  border: solid 1px #cecece;
  -webkit-box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  -moz-box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  visibility: hidden;
  display: none;
}
.card:hover #gram {
  visibility: visible;
  display: inline-block;
}
.card:hover span {
  display: none;
}
#lang {
  background-color: transparent;
  border: none;
  margin-top: 15px;
  margin-right: 5px;
  font-weight: bold;
  transition: all 500ms;
}
#lang:hover {
  color: #48d2c1;
  font-size: 18px;
}
#sort {
  background-color: transparent;
  border: none;
  margin-bottom: 5px;
  margin-right: 5px;
  font-weight: bold;
  font-size: 15px;
  transition: all 500ms;
}
#sort:hover {
  background-color: #e7e7e7;
  color: #48d2c1;
}
body {
  background-color: #e7e7e7;
  font-family: Arial, Helvetica, sans-serif;
}
html {
  margin-bottom: 100px;
  margin-top: 150px;
  direction: ltr;
}

